import React, { Component } from 'react';
import DemoAudio from '../../assets/audios/audio.mp3';

class Guideline121 extends Component {
    render() {
        return (
            <>
                <audio controls autoPlay>
                    <source src={DemoAudio} type="audio/mp3" />
                    <track src="captions_en.vtt" kind="captions" srclang="en" label="english_captions" />
                </audio>
            </>
        );
    }
}
export default Guideline121;
