import React, { Component } from 'react';
import DemoVideo from '../../assets/videos/demo.mp4';

class ResolvedGuideline122 extends Component {
    render() {
        return (
            <div>
                <video width="320" height="240" autoPlay controls>
                    <source src={DemoVideo} type="video/mp4" />
                    <track label="English" kind="captions" srclang="en" src="captions/vtt/sintel-en.vtt" default></track>
                    <track src="audio_desc_en.vtt" kind="descriptions" srclang="en" label="english_description"></track>
                </video>
            </div>
        );
    }
}

export default ResolvedGuideline122;
