import React, { Component } from 'react';


class Guideline241 extends Component {
    constructor(props) {
        super(props);
        this.toggle = this.toggle.bind(this);

    }

    toggle = (id) => {
        if (document.getElementById(id)) {
            // console.log("passed id: ", id);
            let n = document.getElementById(id);
            console.log("element: ", n);
            n.style.display = (n.style.display !== 'none' ? 'none' : '');
        }
    }
    componentWillUnmount() {
        var event = new CustomEvent('noHeader', { detail: {value : false}});
        window.dispatchEvent(event);
    }
    render() {
      var event = new CustomEvent('noHeader', {detail: {value : true}});
      window.dispatchEvent(event);
        return (

            <>
                <a href="#" onClick={this.toggle('navbar')}>Toggle Navigation Bar</a>

                <ul id="navbar">
                    <li><a href="http://target1.html">Link 1</a></li>
                    <li><a href="http://target2.html">Link 2</a></li>
                    <li><a href="http://target3.html">Link 3</a></li>
                    <li><a href="http://target4.html">Link 4</a></li>
                </ul>
                <div id="maincontent">
                    <p>Here is the main content of the page.</p>
                </div>
            </>

        );
    }
}

export default Guideline241;
