import React, { Component } from 'react';
import { BrowserRouter, Switch, Route } from 'react-router-dom';
import LandingPage from '../LandingPage/LandingPage';
import Header from '../Header/Header';
import {
    Guideline111,
    Guideline141,
    Guideline144,
    Guideline221,
    Guideline241,
    Guideline244,
    Guideline222,
    Guideline312,
    Guideline131,
    Guideline143,
    Guideline122,
    Guideline332,
    Guideline321,
    Guideline411,
    Guideline242,
    Guideline246,
    Guideline121,
    Guideline412,
    Guideline311,
    Guideline211
} from '../Guidelines';
import {
    ResolvedGuideline111,
    ResolvedGuideline143,
    ResolvedGuideline144,
    ResolvedGuideline221,
    ResolvedGuideline222,
    ResolvedGuideline312,
    ResolvedGuideline122,
    ResolvedGuideline131,
    ResolvedGuideline321,
    ResolvedGuideline332,
    ResolvedGuideline411,
    ResolvedGuideline242,
    ResolvedGuideline244,
    ResolvedGuideline246,
    ResolvedGuideline412,
    ResolvedGuideline311,
    ResolvedGuideline211,
    ResolvedGuideline241,
    ResolvedGuideline121
} from '../ResolvedGuidelines';

class Routes extends Component {
    render() {
        return (
            <BrowserRouter>
                <Header />
                <Switch>
                    <Route exact path='/' component={LandingPage} />
                    <Route exact path='/landing' component={LandingPage} />
                    <Route exact path='/guidelines-1.1.1' component={Guideline111} />
                    <Route exact path='/resolved-guidelines-1.1.1' component={ResolvedGuideline111} />
                    <Route exact path='/guidelines-1.2.1' component={Guideline121} />
                    <Route exact path='/resolved-guidelines-1.2.1' component={ResolvedGuideline121} />
                    <Route exact path='/guidelines-4.1.2' component={Guideline412} />
                    <Route exact path='/resolved-guidelines-4.1.2' component={ResolvedGuideline412} />
                    <Route exact path='/guidelines-3.1.1' component={Guideline311} />
                    <Route exact path='/resolved-guidelines-3.1.1' component={ResolvedGuideline311} />
                    <Route exact path='/guidelines-2.2.2' component={Guideline222} />
                    <Route exact path='/resolved-guidelines-2.2.2' component={ResolvedGuideline222} />
                    <Route exact path='/guidelines-2.4.1' component={Guideline241} />
                    <Route exact path='/resolved-guidelines-2.4.1' component={ResolvedGuideline241} />
                    <Route exact path='/guidelines-2.1.1' component={Guideline211} />
                    <Route exact path='/resolved-guidelines-2.1.1' component={ResolvedGuideline211} />
                    <Route exact path='/guidelines-2.4.4' component={Guideline244} />
                    <Route exact path='/resolved-guidelines-2.4.4' component={ResolvedGuideline244} />
                    <Route exact path='/guidelines-3.1.2' component={Guideline312} />
                    <Route exact path='/resolved-guidelines-3.1.2' component={ResolvedGuideline312} />
                    <Route exact path='/guidelines-2.2.1' component={Guideline221} />
                    <Route exact path='/resolved-guidelines-2.2.1' component={ResolvedGuideline221} />
                    <Route exact path='/guidelines-1.4.1' component={Guideline141} />
                    <Route exact path='/guidelines-1.4.4' component={Guideline144} />
                    <Route exact path='/resolved-guidelines-1.4.4' component={ResolvedGuideline144} />
                    <Route exact path='/guidelines-1.3.1' component={Guideline131} />
                    <Route exact path='/resolved-guidelines-1.3.1' component={ResolvedGuideline131} />
                    <Route exact path='/guidelines-1.4.3' component={Guideline143} />
                    <Route exact path='/resolved-guidelines-1.4.3' component={ResolvedGuideline143} />
                    <Route exact path='/guidelines-1.2.2' component={Guideline122} />
                    <Route exact path='/resolved-guidelines-1.2.2' component={ResolvedGuideline122} />
                    <Route exact path='/guidelines-2.4.2' component={Guideline242} />
                    <Route exact path='/resolved-guidelines-2.4.2' component={ResolvedGuideline242} />
                    <Route exact path='/guidelines-2.4.6' component={Guideline246} />
                    <Route exact path='/resolved-guidelines-2.4.6' component={ResolvedGuideline246} />
                    <Route exact path='/guidelines-3.3.2' component={Guideline332} />
                    <Route exact path='/resolved-guidelines-3.3.2' component={ResolvedGuideline332} />
                    <Route exact path='/guidelines-3.2.1' component={Guideline321} />
                    <Route exact path='/resolved-guidelines-3.2.1' component={ResolvedGuideline321} />
                    <Route exact path='/guidelines-4.1.1' component={Guideline411} />
                    <Route exact path='/resolved-guidelines-4.1.1' component={ResolvedGuideline411} />
                </Switch>
            </BrowserRouter>
        );
    }
}

export default Routes;
