import React, { Component } from 'react';
import { Helmet } from "react-helmet";


class ResolvedGuideline311 extends Component {
    render() {
        return (
          <div>
            <Helmet htmlAttributes={{ lang : "ar" }}/>
            <p>Arabic text here</p>
          </div>
        );
    }
}

export default ResolvedGuideline311;


