import React, { Component } from 'react';

class Guideline211 extends Component {
    render() {
        return (
            <div>
                <div id="fail1" style= {{height: '5px', overflow: 'auto', display: 'inline-block'}}>
                <label for='inputFail1'>First Field: </label>
                <input type="text" tabindex="-1" id="inputFail1" />
                </div>

                <div id="fail2" style= {{height: '5px', overflow: 'auto', display: 'inline'}}>
                <label for='inputFail2'>Second Field: </label>
                <input type="text" tabindex="-1" id="inputFail2" />
                <label for='selectFail2'>Select: </label>
                <select id="selectFail2" tabindex="-1"></select>
                <label for='textAreaFail2'>TextArea: </label>
                <textarea id="textAreaFail2" tabindex="-1"></textarea>
                </div>
            </div>
        );
    }
}

export default Guideline211;
