import React, { Component } from 'react';
import './App.scss';

import Routes from './components/Routes/Routes';


const NoHeader = React.createContext('false');

class App extends Component {
  render() {
    return (
      <NoHeader.Provider value="false">
        <div className="App">
          <Routes />
        </div>
      </NoHeader.Provider>
    );
  }
}

export default App;
